	module.exports = {"data":{"markdownRemark":{"html":"<p>If the <code>break</code> statement is omitted from a <code>switch</code> statement’s <code>case</code>, the following <code>case</code> statement(s) are executed until a <code>break</code> is encountered. If you have multiple inputs with the same output, you can represent them in a <code>switch</code> statement like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>switch(val) {\n  case 1:\n  case 2:\n  case 3:\n    result = \"1, 2, or 3\";\n    break;\n  case 4:\n    result = \"4 alone\";\n}</code></pre>\n      </div>\n<p>Cases for 1, 2, and 3 will all produce the same result.</p>","fields":{"slug":"/certificates/multiple-identical-options-in-switch-statements/"},"frontmatter":{"title":"Multiple Identical Options in Switch Statements"}}},"pathContext":{"slug":"/certificates/multiple-identical-options-in-switch-statements/"}}

